					{flag:2,tag:'我宅在家',title:'酒精、碘伏、紫外线怎么选？9 种常见消毒产品用法一篇说清楚',linkUrl:'https://mp.weixin.qq.com/s/54yWV3Ll9u1KfPmPa3fUEg',time:'2020-03-03',imgurl:'https://img1.dxycdn.com/2020/0303/294/3399944217341691701-135.jpg'},
					{flag:2,tag:'我要出行',title:'眼镜起雾，耳朵痛？戴口罩的9个困扰，一篇解决',linkUrl:'https://mp.weixin.qq.com/s/u3wN5foNHXMIGBoAfLpgHw',time:'2020-03-02',imgurl:'https://img1.dxycdn.com/2020/0302/352/3399762046303356731-135.jpg'},
				],
				numorsArr:[],//谣言列表
				flagN:false,
			}
		},
		methods:{
			//再复制个数组排序
			sortByKey(arr,key){
				return arr.sort(function(a, b) {
				var y = a[key];
				var x = b[key];
				return ((x < y) ? -1 : ((x > y) ? 1 : 0));
				})
			},
			//获取谣言数据
			getRumors(){
				this.$http.get('/api/rumors',{params:{num:5}}).then(res=>{
					//console.log(res.data.results)
					const temp = this.sortByKey(res.data.results,'_id');
					this.numorsArr = temp;
					this.flagN = true;
					this.$emit('flagN',this.flagN)
					
				})
				.catch(error=>{
					this.$notify({type:'danger',message:'获取谣言失败，刷新重试！'})
				})
			}
		},
		created() {
			this.getRumors()
		},
		updated(){
			this.$nextTick(()=>{
				var mySwiper = new Swiper(".swiper-container",{
					effect : 'coverflow',
					  centeredSlides: true,
					  spaceBetween: '20%',
					  slidesPerView: 'auto',
					  loop: false,
					  pagination:{
						el:'.swiper-pagination',
						clickable:true,
					  },//这样写小圆点就有了
					  loopedSlides: this.numorsArr.length,
					  coverflowEffect: {
						  rotate: 0,
						  stretch: 0,
						  depth: 300,
						  modifier: 1,
						  slideShadows: false,
					  } 
				});
			})
		},
	}
</script>

